import React, { useState, useEffect } from "react";
import LoginPage from "./components/LoginPage";
import AstroAnimation from "./pixi/AstroAnimation";
import MainPage from "./components/MainPage"; // 로그인 성공 시 메인 페이지

function App() {
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    // 로그인 상태를 확인하는 로직 (예시)
    useEffect(() => {
        // 여기에 실제 로그인 상태를 확인하는 로직 추가
        // 예: 토큰 확인, 세션 체크 등
        const token = localStorage.getItem("authToken");
        if (token) {
            setIsLoggedIn(true);
        }
    }, []);

    return (
        <div style={{ position: "relative", width: "100%", height: "100vh" }}>
            {/* PixiJS 애니메이션 */}
            {!isLoggedIn && <AstroAnimation />}

            {/* 로그인 상태에 따른 화면 변경 */}
            {isLoggedIn ? (
                <MainPage />
            ) : (
                <div style={{ position: "absolute", top: 0, left: 0, width: "100%", zIndex: 1 }}>
                    
                </div>
            )}
        </div>
    );
}

export default App;